<template>
	<div class="home">
		<h1>{{ msg }}</h1>
		<ul>
			<li v-for="article in articles" class="article-list">
			<div class="article-list-div">
				<div class="m-img inl-block"><img :src="article.images.small" alt=""></div>
				<div class="m-img inl-block">
					<div>
						<a :href="article.alt">
							<h3>{{ article.title }} {{ article.original_title }}</h3>
						</a>
					</div>
					<div>导演：<a :href="article.directors[0].alt">{{article.directors[0].name}}</a></div>
					<div>主演：
					<li v-for="(cast,index) in article.casts" :class="[castObj]"
					v-if="index < (article.casts.length-1)">
						<a href="cast.alt">
							{{cast.name}}
						</a>
						<span class="fenge"></span>
					</li>
					<li v-else="index == (article.casts.length-1)" :class="[castObj]">
						<a href="cast.alt">
							{{cast.name}}
						</a>
					</li>
					</div>
					<div>类型：
						<li v-for="(genre,index) in article.genres" :class="[castObj]" 
						v-if="index < (article.genres.length-1)">
							{{ genre }} 
							<span class="fenge"></span>
						</li>
						<li v-else="index == (article.genres.length-1)" :class="[castObj]"> 		{{ genre }}
						</li>
					</div>
					<div>年份：{{ article.year }}</div>
					<div> 类型：{{ article.subtype }}</div>
				</div>
				<div>{{date}}</div>
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
	export default{
		name: 'home',
		data () {
			return {
				msg:'电影列表',
				articles:[],
				castObj:'cast',
				date: new Date()
			}
		},
		created: function(){
			this.$http.jsonp('https://api.douban.com/v2/movie/top250?count=15',{},{
				headers: {

				},
				emulateJSON:true
			}).then(function(res){
				console.log(res);
				this.articles = res.data.subjects
			},function(res){
				console.log(res);
			})
		},
		filters:{
			ortitel:function(value){
				
			}
		}
		
	}
	
</script>
<style scoped>
ul li h3{
  list-style: none;
  margin: 0;
  padding: 0;
}
ul li{
padding: 10px 0;
margin: 20px 0;
}

.inl-block{
display: inline-block;
}

.m-img{
  
}
.m-content{
margin-left: 20px;
}
.cast{
		list-style: none;
		display: inline;
		border: none;

	}
a{
	text-decoration:none;
	color:#999;
}
a:hover{
	text-decoration:underline;
}
.article-list{
	width:450px;
	background: #F5F5F5;
	font-size: 11px;
	overflow: hidden;
	border: 1px solid #999;

}
.fenge{
	padding:10px 3px 0px 3px;
	margin-left: 3px;
	border-left: 1px solid #999;
	font-size: 0;
}
.article-list-div{
	width: 400px;
	background: #FFFFFF;
	margin: 20px auto;
}
</style>



